angular.module('diary.services', [])

.constant('SCHEDULE', [
  { value: '0', percent: '0%' },
  { value: '1', percent: '10%' },
  { value: '2', percent: '20%' },
  { value: '3', percent: '30%' },
  { value: '4', percent: '40%' },
  { value: '5', percent: '50%' },
  { value: '6', percent: '60%' },
  { value: '7', percent: '70%' },
  { value: '8', percent: '80%' },
  { value: '9', percent: '90%' },
  { value: '10', percent: '100%' }
])

/**
 * @ngdoc service
 * @name diary.services.Diary
 * @description
 *   
 */
.factory('Diary', function($http, Settings) {
  var moduleUrl = Settings.rootUrl + '/diary';

  function create(data) {
    return $http.post(moduleUrl + '/save', data)
  }

  function remove(id) {
    return $http.post(moduleUrl + '/del', {
      diaryids: id
    });
  }

  function get(id) {
    return $http.get(moduleUrl + '/show', {
      params: { id: id }
    })
  }

  function update(data) {
    return $http.post(moduleUrl + '/update', data)
  }

  return {
    create: create,
    get: get,
    update: update,
    remove: remove,

    /**
     * @ngdoc     method
     * @name      diary.services.Diary#addComment
     * @methodOf  diary.services.Diary
     * @param     {Object}   data    
     *
     * @description
     *   
     *
     * - `diaryid`      ID
     * - `content`      html
     * - `uid`          UID
     * - `tocid`        ID
     * - `touid`        UID
     * - `from`         012android3iphone
     * - `commentcount` 0
     *
     * @returns   {Object}  $q.defer.promise
     */
    addComment: function(data) {
      data = angular.extend({
        from: 1
      }, data);

      return $http.post(moduleUrl + '/addComment', data);
    },

    /**
     * @ngdoc     method
     * @name      diary.services.Diary#index
     * @methodOf  diary.services.Diary
     * @param     {Object}   params    
     * @description
     *   
     *
     * @returns   {HttpPromise}   HttpPromise
     */
    mine: function(params){
      return $http.get(moduleUrl, { params: params });
    },

    /**
     * @ngdoc     method
     * @name      diary.services.Diary#allsubs
     * @methodOf  diary.services.Diary
     * @param     {Object}   params    
     * @description
     *   
     *   
     * @returns   {HttpPromise}   HttpPromise
     */
    allsubs: function(params){
      return $http.get(moduleUrl + '/allsubs', { params: params });
    },


    /**
     * @ngdoc     method
     * @name      diary.services.Diary#other
     * @methodOf  diary.services.Diary
     * @param     {Object}   params    
     * @description
     *   
     *   
     * @returns   {HttpPromise}   HttpPromise
     */
    other: function(params){
      return $http.get(moduleUrl + '/other', { params: params });
    },

    /**
     * @ngdoc     method
     * @name      diary.services.Diary#show
     * @methodOf  diary.services.Diary
     * @param     {String}   id    id
     * @description
     *   
     *   
     * @returns   {HttpPromise}   HttpPromise
     */
    show: function(id){
      return $http.get(moduleUrl + '/show', { params: { id : id } })
    },

    /**
     * @ngdoc     method
     * @name      diary.services.Diary#add
     * @methodOf  diary.services.Diary
     * @param     {String}   diaryDate    'yyyy-MM-dd'
     *
     * @description
     *   
     */
    add: function(diaryDate){
      return $http.get(moduleUrl + '/add' + (diaryDate ? '&diaryDate=' + diaryDate: ''));
    },

    /**
     * @ngdoc     method
     * @name      diary.services.Diary#edit
     * @methodOf  diary.services.Diary
     * @param     {String}   id     id
     *
     * @description
     *   
     */
    edit: function(id) {
      return $http.get(moduleUrl + '/edit&diaryid=' + id);
    },

    /**
     * @ngdoc     method
     * @name      diary.services.Diary#personal
     * @methodOf  diary.services.Diary
     * @param     {Object}   params    
     *
     * @description
     *  
     */
    personal: function(params){
      return $http.get(moduleUrl + '/personal',{ params: params });
    },

    /**
     * @ngdoc     method
     * @name      diary.services.Diary#attention
     * @methodOf  diary.services.Diary
     * @param     {String}   auid     id
     *
     * @description
     *  
     */
    attention: function(auid){
      return $http.get(moduleUrl + '/attention',{params: { auid : auid}})
    },

    /**
     * @ngdoc     method
     * @name      diary.services.Diary#unattention
     * @methodOf  diary.services.Diary
     * @param     {String}   auid     id
     *
     * @description
     *  
     */
    unattention: function(auid){
      return $http.get(moduleUrl + '/unattention',{ params: { auid : auid}})
    },

    /**
     * @ngdoc     method
     * @name      diary.services.Diary#planfromschedule
     * @methodOf  diary.services.Diary
     * @param     {String}   todayDate    'yyyy-MM-dd'
     *
     * @description
     *   
     */
    planfromschedule: function(todayDate){
      return $http.get(moduleUrl + '/planfromschedule',{ params: { todayDate : todayDate }})
    },

    /**
     * @ngdoc     method
     * @name      diary.services.Diary#setting
     * @methodOf  diary.services.Diary
     *
     * @description
     *   
     */
    setting: function(){
      return $http.get(moduleUrl + '/setting')
    },

    /**
     * @ngdoc     method
     * @name      diary.services.Diary#savesetting
     * @methodOf  diary.services.Diary
     * @param     {Object}   data    
     *
     * @description
     *   
     */
    savesetting: function(data){
      return $http.post(moduleUrl + '/savesetting', data)
    }
  };
})

/**
 * @ngdoc service
 * @name diary.services.DiaryUtils
 * @description
 *   
 */
.factory('DiaryUtils', function($filter, $ionicPopup, $timeout, $state, dateFilter, IbPopup, User) {

  return {
    // 
    handleFormData: function(data) {
      return angular.extend({}, data, {
        plantime: $filter('date')(data.plantime, 'yyyy-MM-dd'),
        shareuid: User.addPrefix(data.shareuid)
      })
    },

    //
    setRemind: function($scope, plan){
      $scope.remindConf = {
        start: $scope.workStart.start,
        continueTime: 0.5,
        getStartHours: function() {
          var ret = [];
          for(var i = $scope.workStart.start; i < $scope.workStart.end; i += 0.5) {
            ret.push(i);
          }
          return ret;
        },
        getContinueTimes: function() {
          var ret = [];
          for(var i = 0.5; i <= $scope.workStart.end - this.start; i += 0.5) {
            ret.push(i);
          }
          return ret;
        }
      };

      function popupContinueTime(plan){
        $ionicPopup.show({
          template: '<select class="col" ng-model="remindConf.continueTime" ng-options="(\' \' + item + \' \') for item in remindConf.getContinueTimes() track by item"></select>',
          title: '',
          scope: $scope,
          buttons: [{
            text: ''
          }, {
            text: '',
            type: 'button-positive',
            onTap: function(ok) {
              if(ok) {
                plan.timeremind = $scope.remindConf.start + ',' + ($scope.remindConf.start + +$scope.remindConf.continueTime);
              }
            }
          }]
        })
      }

      $ionicPopup.show({
        template: '<select class="col" ng-model="remindConf.start" ng-options="(item|numToHHmm) for item in remindConf.getStartHours() track by item"></select>',
        title: '',
        scope: $scope,
        buttons: [{
          text: ''
        }, {
          text: '',
          type: 'button-positive',
          onTap: function(ok) {
            if(ok) {
              $timeout(function(){
                popupContinueTime(plan);
              }, 0);
            }
          }
        }]
      });
    },

    //
    delRemind: function(plan){
      plan.timeremind = '';
    },

    //
    selectedStartDate: function($scope){
      $ionicPopup.show({
        template: '<div class="diary-select">' +
          '<input type="date" class="diary-item" ng-model="formData.startDate"  data-tap-disabled ="true" required >' +
          '</div>',
        title: '',
        scope: $scope,
        buttons: [{
          text: ''
        }, {
          text: '',
          type: 'button-positive',
          onTap: function(e) {
            var startDate = $scope.formData.startDate,
              today = new Date;

            if (!startDate) return;

            $timeout(function() {
              if (startDate > today) {
                IbPopup.alert({
                  title: ''
                })
              } else {
                $scope.formData.originalPlan = '';
                $scope.formData.todayDate = dateFilter(startDate, 'yyyy-MM-dd');
                $scope.todayWeekday = dateFilter(startDate, 'EEEE')
              }
            }, 0);
            return true;
          }
        }]
      })
    },

    //
    selectedEndDate: function($scope){
      $ionicPopup.show({
        template: '<div class="diary-select">' +
          '<input type="date" class="diary-item" ng-model="formData.endDate"  data-tap-disabled ="true" required >' +
          '</div>' +
          '<div>' + '{{date}}' + '<div>',
        title: '',
        scope: $scope,
        buttons: [{
          text: ''
        }, {
          text: '',
          type: 'button-positive',
          onTap: function(e) {
            var endDate = Date.parse(new Date($scope.formData.endDate));
            if (!endDate) return;
            $scope.formData.plantime = dateFilter(endDate, 'yyyy-MM-dd');
            $scope.tomorrowWeekday = dateFilter(endDate, 'EEEE')
          }
        }]
      })
    }

  }

})



