/**
 * @ngdoc service
 * @name contact.services.Contact
 * @description
 *   
 */

angular.module('contact.services', [])

  .factory('Contact', function($http,$ionicLoading, Settings) {
    var moduleUrl = Settings.hostUrl + '?r=contact/api';

    return {
      /*
       * @ngdoc    method
       * @name     contact.services.Contact#getDeptList
       * @methodOf contact.services.Contact
       * @description
       *    
       *    `params.deptid`  {string}   id
       * @param   {object}         params  
       * @returns {httppromise}    httppromise
       */
      getDeptList : function(params) {
        return $http.get(moduleUrl + '/deptlist', { params: params })
      },

      /**
       * @ngdoc    method
       * @name     contact.services.Contact#getUserList
       * @methodOf contact.services.Contact
       * @description
       *    
       *    `params.deptid`  {string}   id
       * @param   {object}         params  
       * @returns {httppromise}    httppromise
       */
      getUserList : function(params) {
        return $http.get(moduleUrl + '/userlist', { params: params })
      },

      /**
       * @ngdoc    method
       * @name     contact.services.Contact#search
       * @methodOf contact.services.Contact
       * @description
       *    
       *    `params.deptid`  {string}   id
       * @param   {object}         params  
       * @returns {httppromise}    httppromise
       */
      search: function(params) {
        return $http.get(moduleUrl + '/search', { params: params })
      },

      /**
       * @ngdoc    method
       * @name     contact.services.Contact#getUserInfo
       * @methodOf contact.services.Contact
       * @description
       *    
       * @param   {String}         userid  ID
       * @returns {httppromise}    httppromise
       */
      getUserInfo : function(userid) {
        return $http.get(moduleUrl + '/user', { params: { userid : userid }})
      },

      /**
       * @ngdoc    method
       * @name     contact.services.Contact#getCorpInfo
       * @methodOf contact.services.Contact
       * @description
       *    
       * @returns {httppromise}    httppromise
       */
      getCorpInfo : function() {
        return $http.get(moduleUrl + '/corp')
      },

      /**
       * @ngdoc    method
       * @name     contact.services.Contact#getDeptInfo
       * @methodOf contact.services.Contact
       * @description
       *    
       * @param   {String}         deptid  ID
       * @returns {httppromise}    httppromise
       */
      getDeptInfo : function(deptid) {
        return $http.get(moduleUrl + '/dept',{ params: { deptid : deptid }})
      }
    }
  })

