/**
 * @ngdoc module
 * @name contacts
 * @description
 *   
 */

angular.module('contacts', ['ionic', 'contacts.controllers','contact.services'])

.run(function(Apps) {
  Apps.install({ name: 'contacts', title: '', route: '#/contacts', state: 'contacts.list', icon: 'img/modicon/contacts.png' });
})

.config(function($stateProvider, $urlRouterProvider) {

  $urlRouterProvider.when('/contacts', '/contacts/list/')

  $stateProvider

  .state('contacts', {
    url: "/contacts",
    abstract: true,
    template: '<ion-nav-view name="contacts"></ion-nav-view>',
    resolve: {
      user: function(User) {
        if(!User.isLogin) return User.login();
      }
    }
  })

  .state('contacts.list', {
    url: '/list/:deptid',
    talkingDataLabel: '-',
    views: {
      'contacts': {
        templateUrl: 'templates/contacts/contacts.html',
        controller: 'ContactsCtrl',
      }
    }

  })

  .state('contacts.detail', {
    url: '/detail/:id',
    talkingDataLabel: '-',
    views: {
      'contacts': {
        templateUrl: 'templates/contacts/contacts-detail.html',
        controller: 'ContactsDetailCtrl',        
      }
    }
  })
    .state('contacts.group', {
      url: '/group/:id',
      talkingDataLabel: '-',
      views: {
        'contacts': {
          templateUrl: 'templates/contacts/contact-group-detail.html',
          controller: 'ContactsGroupDetailCtrl',
        }
      }
    })

});