
/**
 * @ngdoc module
 * @name vote
 * @description
 *   
 */

angular.module('vote', ['ionic', 'vote.controllers', 'vote.services'])

  .run(function(Apps) {
    Apps.install({ name: 'vote', title: '', route: '#/vote', state: 'vote.index', icon: 'img/modicon/vote.png' });
  })

  .config(function($stateProvider, $urlRouterProvider, UserProvider) {

    $urlRouterProvider.when('/vote', '/vote/index');


    $stateProvider
      .state('vote', {
        url: "/vote",
        abstract: true,
        template: '<ion-nav-view name="vote"></ion-nav-view>',
        resolve: UserProvider.resolve
      })

      .state('vote.index', {
        url: '/index',
        cache: false,
        talkingDataLabel: '-',
        views: {
          'vote': {
            templateUrl: 'templates/vote/vote-index.html',
            controller: 'VoteIndexCtrl'
          }
        }
      })

      .state('vote.mine', {
        url: '/mine',
        cache: false,
        talkingDataLabel: '-',
        views: {
          'vote': {
            templateUrl: 'templates/vote/vote-index.html',
            controller: 'VoteIndexCtrl'
          }
        }
      })

      //
      .state('vote.view', {
        url: '/view/:id',
        talkingDataLabel: '-',
        cache: false,
        views: {
          'vote': {
            templateUrl: 'templates/vote/vote-view.html',
            controller: 'VoteViewCtrl'
          }
        }
      })

  });