angular.module('activity.services', [])

/**
 * @ngdoc service
 * @name  activity.services.Activity
 * @description
 *   
 */
.factory('Activity', function($http, Utils, Settings) {
  // 
  var moduleUrl = Settings.hostUrl + '?r=activity';

  var Activity = {
    /**
     * @ngdoc service
     * @name  activity.services.list
     * @param    {object}       params
     * @description
     *    
     *    - `params.type`  {string}  allme
     *    - `params.page`  {number=} 1
     * 
     * @returns  {HttpPromise}  HttpPromise
     */
    list: function(params) {
      return $http.get(moduleUrl + '/activityapi/index', {
        params: params
      });
    },

    /**
     * @ngdoc service
     * @name  activity.services.view
     * @param    {object}    params
     * @description
     *    
     *    - `params.id`  {string}   id
     *    
     * @returns  {HttpPromise}  HttpPromise
     */
    view: function(params) {
      return $http.get(moduleUrl + '/activityapi/detail', {
        params: params
      });
    },

    /**
     * @ngdoc service
     * @name  activity.services.formField
     * @param    {object}    params
     * @description
     *    
     *    - `params.id`  {string}   id
     *    
     * @returns  {HttpPromise}  HttpPromise
     */
    formField: function(params) {
      return $http.get(moduleUrl + '/signupapi/create', {
        params: params
      });
    },

    /**
     * @ngdoc service
     * @name  activity.services.createSignUp
     * @param    {object}    params
     * @description
     *    
     *    - `params.moduleid`  {string}  id
     *    - `params.uid`       {string}  id
     *    - `params.name`      {string}  
     *    - `params.phone`     {string}  
     *    - `params.data1`     {string}  
     *    ...
     *    
     * @returns  {HttpPromise}  HttpPromise
     */
    createSignUp: function(params) {
      return Utils.postJsonData(moduleUrl + '/signupapi/store', params);
    },

    /**
     * @ngdoc service
     * @name  activity.services.updateSignUp
     * @param    {object}    params
     * @description
     *    
     *    - `params.moduleid`    {string}  id
     *    - `params.formdataid`  {string}  id
     *    - `params.name`        {string}  
     *    - `params.phone`       {string}  
     *    - `params.data1`       {string}  
     *    ...
     *    
     * @returns  {HttpPromise}  HttpPromise
     */
    updateSignUp: function(params) {
      return Utils.postJsonData(moduleUrl + '/signupapi/update', params);
    },

    /**
     * @ngdoc service
     * @name  activity.services.deleteSignUp
     * @param    {object}    params
     * @description
     *    
     *    - `params.moduleid`    {string}  id
     *    - `params.formdataid`  {string}  id
     *    
     * @returns  {HttpPromise}  HttpPromise
     */
    deleteSignUp: function(params) {
      return $http.post(moduleUrl + '/signupapi/del', params);
    },

    /**
     * @ngdoc service
     * @name  activity.services.signUpList
     * @param    {object}    params
     * @description
     *    
     *    - `params.id`  {string}  id
     *    
     * @returns  {HttpPromise}  HttpPromise
     */
    signUpList: function(params) {
      return $http.get(moduleUrl + '/signupapi/detail', {
        params: params
      });
    },

    /**
     * @ngdoc service
     * @name  activity.services.arrangeList
     * @param    {object}    params
     * @description
     *    
     *    - `params.id`  {string}  id
     *    
     * @returns  {HttpPromise}  HttpPromise
     */
    arrangeList: function(params) {
      return $http.get(moduleUrl + '/arrangementapi/detail', {
        params: params
      });
    }
  };

  return Activity;
})

.factory('ActivityList', function(Activity, Utils) {
  return function(defaultParams) {
    var that = this;
    this.loading = false;
    this.hasMore = true;
    this.page = 1;
    this.list = [];
    // 
    this.load = function() {
      var promise;

      this.loading = true;

      promise = Activity.list(angular.extend({}, defaultParams, {
        page: that.page
      }))
      .success(function(res) {
        if(res.isSuccess) {
          that.list = that.list.concat(res.data);
          that.hasMore = res.hasmore;
          that.page++;
          console.log(res)
        } else {
          Utils.promptNoPermission();
        }
      });

      promise.finally(function() {
        that.loading = false;
      });

      return promise;
    };
    // 
    this.reload = function() {
      this.page = 1;
      this.list.length = 0;
      this.hasMore = true;
      return this.load();
      // .finally(function() {
      //   $scope.$broadcast('scroll.refreshComplete');
      // });
    }
  }
})

/**
 * @ngdoc filter
 * @name activity.services.filter:phoneCrawler
 * @function
 * @description
 *  
 *  
 * @param   {string}  content  
 * @returns {string}  
 */
.filter('phoneCrawler', function(REGEXP_ENUM) {
  var reg = /\b1\d{10}\b/g ; ///1\d{10}/g
  return function(content) {
    content = content || '';
    return content.replace(reg, function(phone) {
      return '<a href="tel:' + phone + '">' + phone + '</a>'
    });
  }
})