/**
 * @ngdoc module
 * @name assignment.services
 * @description
 *   
 */

angular.module('assignment.services', [])


/**
 * @ngdoc service
 * @name Assignment
 * @module assignment.services
 * @description
 *   
 */

.factory('Assignment', function($rootScope, $http, $state, $filter, $ionicLoading, Settings, Utils, User) {
  var moduleUrl = Settings.rootUrl + '/assignment';

  var DATETIME_FORMAT = 'yyyy-MM-dd HH:mm';

  //  0 1 2 3 4 
  var operation = {
    // 
    designee: {
      '0': ['edit', 'remove', 'urge', 'cancel', 'delay', 'remind'],
      '1': ['edit', 'remove', 'done', 'cancel', 'delay', 'remind'],
      '2': ['edit', 'remove', 'stamp', 'restart'],
      '3': ['edit', 'remove', 'restart'],
      '4': ['edit', 'remove', 'restart']
    },
    // 
    charge: {
      '0': ['done', 'remind'],
      '1': ['done', 'applyDelay', 'applyCancel', 'remind'],
      '2': ['restart'],
      '3': ['restart'],
      '4': ['restart']
    }
    // 
  };

  return {
    getRole: function(task) {
      if(User.uid == task.designeeuid) {
        return 'designee';
      } else if(User.uid == task.chargeuid) {
        return 'charge';
      } else if(task.participantuid.split(',').indexOf(User.uid) !== -1) {
        return 'participant';
      }
      return '';
    },

    getRoleText: function(task) {
      var text = {
        'charge': '',
        'designee': '',
        'participant': ''
      };
      return text[this.getRole(task)] || '';
    },

    getStampPath: function(value, stamps) {
      var path = '';
      
      angular.forEach(stamps, function(stamp) {
        if(value == stamp.value) {
          path = stamp.stampPath;
        }
        return false;
      });

      return path;
    },

    /**
     * @ngdoc method
     * @name Assignment#getEnableOperation
     * @description 
     *    
     * @param {Object} task
     * @returns {Array} 
     */
    getEnableOperation: function(task) {
      var op = User.uid == task.designeeuid ? 
      operation.designee:
      User.uid == task.chargeuid ?
      operation.charge:
      {};

      return op[task.status] || [];
    },

    /**
     * @ngdoc method
     * @name Assignment#convertDateTime
     * @description 
     *   
     *   datetime-local yyyy-MM-ddThh:ii
     *    yyyy-MM-dd hh:ii
     * @param {Date} date 
     * @returns {String}  
     */
    convertDateTime: function(date) {
      date = angular.isDate(date) ? date : new Date(date);

      if(Utils.isDateTimeLocalSupport) {
        date = new Date(+date - (date.getTimezoneOffset() * 60000));
        return date.toJSON().slice(0, 16);
      } else {
        return $filter('date')(date, DATETIME_FORMAT);
      }
    },

    formatDatetime: function(date) {
      return $filter('date')(date, DATETIME_FORMAT);
    },

    /**
     * @ngdoc method
     * @name Assignment#correctDateTime
     * @description 
     *    datetime-local 
     * @param {String} dateStr
     * @returns {String}
     */
    correctDateTime: function(dateStr) {
      var date = new Date(dateStr);

      if(Utils.isDateTimeLocalSupport) {
        date = new Date(+date + (date.getTimezoneOffset() * 60000));
      }

      return this.formatDatetime(date);
    },

    /**
     * @ngdoc method
     * @name Assignment#correctFormData
     * @description 
     *   
     * @param {Object} form 
     * @returns {Object}    
     */
    correctFormData: function(form) {
      form = angular.extend({}, form);

      form.chargeuid = User.addPrefix(form.chargeuid);
      form.participantuid = User.addPrefix(form.participantuid) || '';

      // form.starttime = this.correctDateTime(form.starttime) || '';
      // form.endtime = this.correctDateTime(form.endtime);
      form.starttime = this.formatDatetime(form.starttime);
      form.endtime = this.formatDatetime(form.endtime);

      form.description = form.description || '';
      // 
      form.attachmentid = '';

      return form;
    },

    /**
     * @ngdoc method
     * @name Assignment#get
     * @description 
     *    
     * @param {Object}   params      id
     * @returns {Object} promise
     */
    get: function(params) {
      return $http.get(moduleUrl + '/edit', {
        params: params
      })
    },

    /**
     * @ngdoc method
     * @name Assignment#get
     * @description 
     *    
     * @param {Object}   params      id
     * @returns {Object} promise
     */
    getFullInfo: function(params) {
      params = params || {};
      params.assignmentId = params.id;
      return $http.get(moduleUrl + '/show', {
        params: params
      })
    },

    /**
     * @ngdoc method
     * @name Assignment#create
     * @description 
     *    
     * @param {Object} data
     * @returns {Object} promise
     */
    create: function(data) {
      data.formhash = User.formhash
      return $http.post(moduleUrl + '/add&addsubmit=1', data)
      .success(function(res) {
        $rootScope.$broadcast('assignment.create', data, res);
      });
    },

    /**
     * @ngdoc method
     * @name Assignment#update
     * @description 
     *   
     * @param {String}     id      id
     * @param {Object}     data   
     * @returns {Object}   promise
     */
    update: function(id, data) {
      return $http.post(moduleUrl + '/edit&id=' + id, data)
      .success(function(res) {
        $rootScope.$broadcast('assignment.update', id, data, res);
      });
    },

    /**
     * @ngdoc method
     * @name Assignment#remove
     * @description 
     *   
     * @param {Object}   params   
     *   params.id  {String} id
     * @returns {Object} promise
     */
    remove: function(params) {
      return $http.post(moduleUrl + '/del', params)
    },

    /**
     * @ngdoc method
     * @name Assignment#getSubordinates
     * @description 
     *   
     * @param {Object}   params   
     *   params.uid            {String} id
     * @returns {Object} promise
     */
    getSubordinates: function(params) {
      return $http.get(Settings.rootUrl + '/assignmentunfinished/subList&op=getsubordinates', {
        params: params
      })
      .error(Settings.requestError);
    },

    /**
     * @ngdoc method
     * @name Assignment#getSubordinates
     * @description 
     *   
     * @param {Object}   params   
     *   params.id            {String} id
     *   params.stamp         {String} 
     * @returns {Object} promise
     */
    stamp: function(params) {
      return $http.post(Settings.rootUrl + '/assignmentunfinished/ajaxEntrance&op=stamp', params)
      .error(Settings.requestError);
    }
  };
})

/**
 * @ngdoc service
 * @name AssignmentUnfinished
 * @module assignment.services
 * @description
 *   
 */
.factory('AssignmentUnfinished', function($rootScope, $http, Settings, IbPopup) {
  var url = Settings.rootUrl + '/assignmentunfinished';

  function serverError(res) {
    if(!res.isSuccess) {
      IbPopup.tip(res.msg);
    }
  }

  var ret = {
    /**
     * @ngdoc method
     * @name AssignmentUnfinished#get
     * @description 
     *   
     * @returns {Object} promise
     */
    get: function() {
      return $http.get(url).error(Settings.requestError);
    },

    getSubList: function(params) {
      return $http.get(url + '/subList', {
        params: params
      })
      .error(Settings.requestError);
    },
    /**
     * @ngdoc method
     * @name AssignmentUnfinished#urge
     * @description 
     *   
     * @param {Object}   params   
     *   params.id  {String} id
     * @returns {Object} promise
     */
    urge: function(params) {
      return $http.post(url + '/ajaxEntrance&op=push', params)
      .success(serverError)
      .success(function(res) {
        $rootScope.$broadcast('assignment.urge', params, res);
      })
      .error(Settings.requestError);
    },

    /**
     * @ngdoc method
     * @name AssignmentUnfinished#done
     * @description 
     *   
     * @param {Object}   params   
     *   params.id  {String} id
     * @returns {Object} promise
     */
    done: function(params) {
      return $http.post(url + '/ajaxEntrance&op=toFinished', params)
      .success(serverError)
      .success(function(res) {
        $rootScope.$broadcast('assignment.done', params, res);
      })
      .error(Settings.requestError);
    },

    /**
     * @ngdoc method
     * @name AssignmentUnfinished#restart
     * @description 
     *   
     * @param {Object}   params   
     *   params.id  {String} id
     * @returns {Object} promise
     */
    restart: function(params) {
      return $http.post(url + '/ajaxEntrance&op=restart', params)
      .success(serverError)
      .success(function(res) {
        $rootScope.$broadcast('assignment.restart', params, res);
      })
      .error(Settings.requestError);
    },

    /**
     * @ngdoc method
     * @name AssignmentUnfinished#applyDelay
     * @description 
     *   
     * @param {Object}     params   
     *   params.id            {String} id
     *   params.starttime     {String}  yyyy-MM-dd HH:mm
     *   params.endtime       {String} 
     *   params.delayReason   {String} 
     * @returns {Object} promise
     */
    applyDelay: function(params) {
      return $http.post(url + '/ajaxEntrance&op=applyDelay', params)
      .success(serverError)
      .error(Settings.requestError);
    },

    /**
     * @ngdoc method
     * @name AssignmentUnfinished#applyCancel
     * @description 
     *   
     * @param {Object}     params   
     *   params.id            {String} id
     *   params.cancelReason  {String} 
     * @returns {Object} promise
     */
    applyCancel: function(params) {
      return $http.post(url + '/ajaxEntrance&op=applyCancel', params)
      .success(serverError)
      .error(Settings.requestError);
    },

    /**
     * @ngdoc method
     * @name AssignmentUnfinished#dealDelayApply
     * @description 
     *   
     * @param {Object}   params   
     *   params.id       {String} id
     *   params.agree    {Number} 0 1
     * @returns {Object} promise
     */
    dealDelayApply: function(params) {
      return $http.post(url + '/ajaxEntrance&op=runApplyDelayResult', params)
      .success(serverError)
      .error(Settings.requestError);
    },

    /**
     * @ngdoc method
     * @name AssignmentUnfinished#dealCancelApply
     * @description 
     *   
     * @param {Object}   params   
     *   params.id       {String} id
     *   params.agree    {Number} 0 1
     * @returns {Object} promise
     */
    dealCancelApply: function(params) {
      return $http.post(url + '/ajaxEntrance&op=runApplyCancelResult', params)
      .success(serverError)
      .error(Settings.requestError);
    },

    /**
     * @ngdoc method
     * @name AssignmentUnfinished#delay
     * @description 
     *   
     * @param {Object}   params   
     *   params.id       {String} id
     * @returns {Object} promise
     */
    delay: function(params) {
      return $http.post(url + '/ajaxEntrance&op=delay', params)
      .success(serverError)
      .error(Settings.requestError);
    },

    /**
     * @ngdoc method
     * @name AssignmentUnfinished#cancel
     * @description 
     *   
     * @param {Object}   params   
     *   params.id       {String} id
     * @returns {Object} promise
     */
    cancel: function(params) {
      return $http.post(url + '/ajaxEntrance&op=cancel', params)
      .success(serverError)
      .error(Settings.requestError);
    },

    /**
     * @ngdoc method
     * @name AssignmentUnfinished#remind
     * @description 
     *   
     * @param {Object}   params   
     *   params.id            {String} id
     *   params.remindTime    {String} 
     *   params.remindContent {String} 
     * @returns {Object} promise
     */
    remind: function(params) {
      return $http.post(url + '/ajaxEntrance&op=remind&remindsubmit=1', params)
      .success(serverError)
      .error(Settings.requestError);
    },

    getRemindData: function(params) {
      return $http.get(url + '/ajaxEntrance&op=remind', { params: params })
      .success(serverError)
      .error(Settings.requestError);
    }
  };

  return ret;
})

/**
 * @ngdoc service
 * @name AssignmentFinished
 * @module assignment.services
 * @description
 *   
 */
.factory('AssignmentFinished', function($rootScope, Settings, IbList) {
  var assignmentFinished = new IbList(Settings.rootUrl + '/assignmentfinished', {
    idAttr: 'assignmentid'
  });
  // 
  //  search 
  //  () 
  assignmentFinished.search = function(keyword) {
    if(keyword.trim() === '') {
      return this.fetch({
        offset: 0,
      }, true);
    } else {
      return this.fetch({
        offset: 0,
        param: 'search',
        keyword: keyword
      }, true);
    }

  };

  return assignmentFinished;
})


/**
 * @ngdoc filter
 * @name assignment.services.filter:asmStatusText
 * @function
 * @description
 *  
 *  
 * @param   {string}  status  
 * @returns {string}  
 */
.filter('asmStatusText', function() {
  var statusMap = {
    0: '',
    1: '',
    2: '',
    3: '',
    4: ''
  };

  return function(status) {
    return statusMap[+status] || '';
  };
})